.health-score-card {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: all 0.2s ease;
}
.health-score-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  border-color: #d1d5db;
}
.health-score-card .card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2rem;
}
.health-score-card .card-header .card-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}
.health-score-card .card-header .score-label {
  padding: 0.5rem 1rem;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.025em;
}
.health-score-card .card-header .score-label.health-excellent {
  background: #dcfce7;
  color: #166534;
}
.health-score-card .card-header .score-label.health-good {
  background: #dbeafe;
  color: #1e40af;
}
.health-score-card .card-header .score-label.health-fair {
  background: #fef3c7;
  color: #d97706;
}
.health-score-card .card-header .score-label.health-poor {
  background: #fecaca;
  color: #dc2626;
}
.health-score-card .card-header .score-label.health-critical {
  background: #fecaca;
  color: #991b1b;
}
.health-score-card .score-display {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
.health-score-card .score-display .score-circle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  position: relative;
  flex-shrink: 0;
}
.health-score-card .score-display .score-circle.health-excellent {
  background: linear-gradient(135deg, #dcfce7 0%, #bbf7d0 100%);
  color: #166534;
}
.health-score-card .score-display .score-circle.health-good {
  background: linear-gradient(135deg, #dbeafe 0%, #bfdbfe 100%);
  color: #1e40af;
}
.health-score-card .score-display .score-circle.health-fair {
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  color: #d97706;
}
.health-score-card .score-display .score-circle.health-poor {
  background: linear-gradient(135deg, #fecaca 0%, #fca5a5 100%);
  color: #dc2626;
}
.health-score-card .score-display .score-circle.health-critical {
  background: linear-gradient(135deg, #fecaca 0%, #f87171 100%);
  color: #991b1b;
}
.health-score-card .score-display .score-circle .score-value {
  font-size: 3rem;
  font-weight: 800;
  line-height: 1;
}
.health-score-card .score-display .score-circle .score-percent {
  font-size: 1.5rem;
  font-weight: 600;
  margin-left: 0.25rem;
}
.health-score-card .score-display .score-details {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}
.health-score-card .score-display .score-details .detail-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.health-score-card .score-display .score-details .detail-item .detail-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.health-score-card .score-display .score-details .detail-item .detail-value {
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
}
.health-score-card .progress-bar {
  width: 100%;
  height: 0.5rem;
  background: #e5e7eb;
  border-radius: 0.25rem;
  overflow: hidden;
  margin-bottom: 1.5rem;
}
.health-score-card .progress-bar .progress-fill {
  height: 100%;
  border-radius: 0.25rem;
  transition: width 0.3s ease;
}
.health-score-card .progress-bar .progress-fill.health-excellent {
  background: linear-gradient(90deg, #22c55e 0%, #16a34a 100%);
}
.health-score-card .progress-bar .progress-fill.health-good {
  background: linear-gradient(90deg, #3b82f6 0%, #2563eb 100%);
}
.health-score-card .progress-bar .progress-fill.health-fair {
  background: linear-gradient(90deg, #f59e0b 0%, #d97706 100%);
}
.health-score-card .progress-bar .progress-fill.health-poor {
  background: linear-gradient(90deg, #ef4444 0%, #dc2626 100%);
}
.health-score-card .progress-bar .progress-fill.health-critical {
  background: linear-gradient(90deg, #dc2626 0%, #991b1b 100%);
}
.health-score-card .health-description .description-text {
  font-size: 0.875rem;
  color: #6b7280;
  line-height: 1.6;
  margin: 0;
}

@media (max-width: 768px) {
  .health-score-card {
    padding: 1.5rem;
  }
  .health-score-card .card-header {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
    text-align: center;
  }
  .health-score-card .score-display {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
  }
  .health-score-card .score-display .score-circle {
    width: 6rem;
    height: 6rem;
  }
  .health-score-card .score-display .score-circle .score-value {
    font-size: 2.25rem;
  }
  .health-score-card .score-display .score-circle .score-percent {
    font-size: 1.125rem;
  }
  .health-score-card .score-display .score-details {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
}
